import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { AppRoutes } from "@core/enums";
import { SnackBarService } from "@core/services";
import { setGlobals } from "@core/store/actions/global.actions";
import { Actions, createEffect, Effect, ofType } from "@ngrx/effects";
import { Store } from "@ngrx/store";
import { TranslateService } from "@ngx-translate/core";
import { IBusiness } from "@shared/interfaces";
import { IUser } from "@shared/interfaces/user/user";
import { BusinessService, UsersService } from "@shared/services";
import { of } from "rxjs";
import { catchError, exhaustMap, map } from "rxjs/operators";
import * as BusinessActions from "../actions/business.actions";

@Injectable()
export class BusinessEffects {
  loadBusinesses$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.loadBusinesses),
      exhaustMap((action) =>
        this.businessService.getBusinessList(action.params).pipe(
          map((response: any) => BusinessActions.loadBusinessesSuccess(response)),
          catchError((error) => of(BusinessActions.loadBusinessesFailure({ error })))
        )
      )
    )
  );

  getBusiness$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.getBusiness),
      exhaustMap((action) =>
        this.businessService.getBusinessDetail(action.id).pipe(
          map((response: any) => BusinessActions.getBusinessSuccess(response)),
          catchError((error) => of(BusinessActions.getBusinessesFailure({ error })))
        )
      )
    )
  );

  getBusinessTypes$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.getBusinessTypes),
      exhaustMap((action) =>
        this.businessService.getBusinessTypes().pipe(
          map((response: any) => BusinessActions.getBusinessTypesSuccess(response)),
          catchError((error) => of(BusinessActions.getBusinessesTypesFailure({ error })))
        )
      )
    )
  );

  saveBusiness$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.saveBusiness),
      exhaustMap((action) =>
        this.businessService.saveBusiness(action.payload).pipe(
          map((response: any) => BusinessActions.saveBusinessSuccess(response.data)),
          catchError((error) => of(BusinessActions.saveBusinessesFailure({ error })))
        )
      )
    )
  );

  updateBusiness$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.updateBusiness),
      exhaustMap((action) =>
        this.businessService.updateBusiness(action.payload).pipe(
          map((response: any) => BusinessActions.updateBusinessSuccess(response)),
          catchError((error) => of(BusinessActions.updateBusinessesFailure({ error })))
        )
      )
    )
  );

  deleteBusiness$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.deleteBusiness),
      exhaustMap((action) =>
        this.businessService.deleteBusiness(action.id).pipe(
          map((response: any) => BusinessActions.deleteBusinessSuccess(response)),
          catchError((error) => of(BusinessActions.deleteBusinessesFailure({ error })))
        )
      )
    )
  );

  @Effect({ dispatch: false })
  saveBusinessSuccess = this.actions$.pipe(
    ofType<any>(BusinessActions.saveBusinessSuccess),
    map((business: IBusiness) => {
      this.store.dispatch(setGlobals({ globals: { business } }));
      this.router.navigateByUrl(AppRoutes.BUSINESS);
      this.snackBar.open(this.translate.instant("business_add_success"));
    })
  );

  @Effect({ dispatch: false })
  updateBusinessSuccess = this.actions$.pipe(
    ofType<any>(BusinessActions.updateBusinessSuccess),
    map((businessData: any) => {
      this.store.dispatch(setGlobals({ globals: { business: businessData.data } }));
      this.router.navigateByUrl(AppRoutes.BUSINESS);
      this.snackBar.open(this.translate.instant("business_update_success"));
    })
  );

  @Effect({ dispatch: false })
  deleteBusinessSuccess = this.actions$.pipe(
    ofType<any>(BusinessActions.deleteBusinessSuccess),
    map(({ payload }) => {
      this.router.navigateByUrl(AppRoutes.BUSINESS);
      this.store.dispatch(setGlobals({ globals: { business: null } }));
      this.store.dispatch(BusinessActions.loadBusinesses(null));
      this.snackBar.open(this.translate.instant("business_delete_success"));
    })
  );

  @Effect({ dispatch: false })
  setParam = this.actions$.pipe(
    ofType<any>(BusinessActions.setParameters),
    map((res) => {
      this.store.dispatch(BusinessActions.loadBusinesses({ params: res.params }));
    })
  );

  /**
   * Create new user for business.
   */
  createUserForBusiness$ = createEffect(() =>
    this.actions$.pipe(
      ofType(BusinessActions.createUserForBusiness),
      exhaustMap((action) =>
        this.userService.createNewUser(action.payload).pipe(
          map((response: any) => BusinessActions.createUserForBusinessSuccess(response.data)),
          catchError((error) => of(BusinessActions.createUserForBusinessFailure({ error })))
        )
      )
    )
  );

  @Effect({ dispatch: false })
  createUserForBusinessSuccess$ = this.actions$.pipe(
    ofType<any>(BusinessActions.createUserForBusinessSuccess),
    map((user: IUser) => {
      this.snackBar.open(this.translate.instant("mod_user.add_success"));
    })
  );

  constructor(
    private actions$: Actions,
    private businessService: BusinessService,
    private router: Router,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    private userService: UsersService,
    private store: Store<any>
  ) {}
}
