import { Component, EventEmitter, Inject, OnInit, Output, ViewChild } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { BaseComponent } from "@core/components";
import { StorageKeys } from "@core/enums";
import { CustomValidators } from "@core/utilities";
import { FileUploadComponent } from "@shared/file-upload";
import { IRole } from "@shared/interfaces/user/role";
import { UsersService } from "@shared/services";
import { Observable } from "rxjs";

/**
 * @title Dialog Overview
 */
@Component({
  selector: "app-add-user-modal",
  templateUrl: "add-user-modal.component.html",
})
export class AddUserModalComponent extends BaseComponent implements OnInit {
  @Output() saveOrUpdateClicked: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild("fileUploadCmp") fileUploadCmp: FileUploadComponent;
  errors: Observable<Error>;
  userForm: FormGroup;
  loading: boolean = true;
  imageUrl: string;
  title: string;
  buttonText: string;

  /**
   *
   * @param {MatDialogRef<AddUserModalComponent>} dialogRef
   * @param data
   * @param {UsersService} userService
   */
  constructor(
    public dialogRef: MatDialogRef<AddUserModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private userService: UsersService
  ) {
    super();
  }

  ngOnInit() {
    if (this.data.userId) {
      this.createUpdateForm(this.data.userId);
      this.getAndSetUser(this.data.userId);
      this.title = this.getTranslatedString("mod_user.update_user");
      this.buttonText = this.getTranslatedString("common.update");
    } else {
      this.createUserForm();
      this.title = this.getTranslatedString("mod_user.add_user");
      this.buttonText = this.getTranslatedString("common.save");
    }

    this.listenErrors();
    this.getRoles();
  }

  /**
   * Handle no button clicked.
   */
  onNoClick(): void {
    this.dialogRef.close();
  }

  /**
   * Handle user form submission.
   */
  handleSubmit() {
    if (
      this.userForm.valid &&
      !this.fileUploadCmp.errors.invalidFileFormat &&
      !this.fileUploadCmp.errors.invalidFileSize
    ) {
      this.saveOrUpdateClicked.emit(this.userForm.value);
    }
  }

  /**
   *
   * @private
   */
  private createUserForm() {
    this.userForm = this.formBuilder.group({
      mobileNumber: ["", [Validators.required, CustomValidators.mobileNumber()]],
      namespace: this.data.namespace,
      namespaceId: this.data.namespaceId,
      role: new FormControl({ value: "", disabled: true }),
      roleCode: [],
      status: true,
    });
  }

  /**
   * Listen to errors observables and set them to the form.
   * @private
   */
  private listenErrors() {
    this.data.errors$.subscribe((err) => {
      if (err && this.userForm) {
        this.errorMessageService.handleServerSideError(this.userForm, err);
      }
    });
  }

  /**
   *
   * @param {number} userId
   * @private
   */
  private createUpdateForm(userId: number) {
    this.userForm = this.formBuilder.group({
      id: userId,
      status: true,
      namespace: this.data.namespace,
      namespaceId: this.data.namespaceId,
      email: ["", [Validators.required, Validators.email]],
      name: ["", [Validators.required, Validators.minLength(3), Validators.maxLength(32)]],
      mobileNumber: ["", [Validators.required, CustomValidators.mobileNumber()]],
      role: new FormControl({ value: "", disabled: true }),
      roleId: [],
      imageId: [],
      file: [],
    });
  }

  /**
   *
   * @param {number} userId
   * @private
   */
  private getAndSetUser(userId: number) {
    this.userService.getUserById(userId).subscribe((user: any) => {
      this.userForm.patchValue(user.data);
      this.imageUrl = user.image ? user.image.url : "";
    });
  }

  /**
   *
   * @private
   */
  private getRoles() {
    const currentBuilding = this.storage.get(StorageKeys.ACTIVE_BUILDING);
    let params = null;
    if (currentBuilding) {
      params = { buildingId: currentBuilding.id };
    }
    this.userService.getRoles(params).subscribe(
      (res: any) => {
        this.loading = false;
        const role$ = res.data.find((role: IRole) => role.title === this.data.role);
        this.userForm.get("roleCode").setValue(role$.code);
        this.userForm.get("role").setValue(this.data.role);
      },
      (error) => (this.loading = false)
    );
  }

  /**
   *
   * @param {File} file
   */
  onFileUpload(file: File) {
    this.userForm.get("file").setValue(file);
  }
}
