import { IGlobal } from "@core/interfaces/global";
import { createReducer, on } from "@ngrx/store";
import { IBusiness } from "@shared/interfaces";
import { IBuilding } from "app/features/admin/modules/buildings/interfaces";
import { INews } from "app/features/admin/modules/news/interfaces/news";
import { ITicket } from "../../../features/admin/modules/tickets/interfaces";
import * as GlobalActions from "../actions/global.actions";

export interface GlobalState {
  globals: IGlobal;
  globalBusiness: IBusiness;
  globalBuilding: IBuilding;
  globalNews: INews;
  globalTicket: ITicket;
}

export const initialGlobalState: GlobalState = {
  globals: null,
  globalBusiness: null,
  globalBuilding: null,
  globalNews: null,
  globalTicket: null,
};

export const globalReducer = createReducer(
  initialGlobalState,

  on(GlobalActions.setGlobals, (state, action) => ({
    ...state,
    globals: action.globals,
  })),

  on(GlobalActions.setGlobalBusiness, (state, action) => ({
    ...state,
    globalBusiness: action.globalBusiness,
  })),

  on(GlobalActions.setGlobalBuilding, (state, action) => ({
    ...state,
    globalBuilding: action.globalBuilding,
  })),

  on(GlobalActions.setGlobalNews, (state, action) => ({
    ...state,
    globalNews: action.globalNews,
  })),

  on(GlobalActions.setGlobalTicket, (state: GlobalState, action) => ({
    ...state,
    globalTicket: action.globalTicket,
  }))
);
