import { createFeatureSelector } from "@ngrx/store";
import * as fromGlobal from "./global.reducer";

export interface CoreModuleState {
  global: fromGlobal.GlobalState;
}

export interface State {
  coreModule: CoreModuleState;
}

export const coreReducers = {
  global: fromGlobal.globalReducer,
};

export const selectCoreModuleState = createFeatureSelector<CoreModuleState>("coreModule");
