import { Directive, Input, OnInit, TemplateRef, ViewContainerRef } from "@angular/core";
import { PermissionsService } from "@core/services";

@Directive({
  selector: "[appHasAccess]",
})
export class HasAccessDirective implements OnInit {
  @Input() appHasAccess: string;

  constructor(
    private templateRef: TemplateRef<any>,
    private viewContainer: ViewContainerRef,
    private permissionsService: PermissionsService
  ) {}

  ngOnInit(): void {
    this.permissionsService.checkAuthorization(this.appHasAccess).subscribe((authorized) => {
      if (authorized) {
        this.viewContainer.createEmbeddedView(this.templateRef);
      } else {
        this.viewContainer.clear();
      }
    });
  }
}
