import { Component, EventEmitter, Inject, OnInit, Output } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { BaseComponent } from "@core/components";
import { IUser } from "@shared/interfaces/user/user";
import { UsersService } from "@shared/services";

/**
 * @title Dialog Overview
 */
@Component({
  selector: "app-user-details-modal",
  templateUrl: "user-details-modal.component.html",
})
export class UserDetailsModalComponent extends BaseComponent implements OnInit {
  @Output() saveOrUpdateClicked: EventEmitter<any> = new EventEmitter<any>();
  loading: boolean = true;
  user: IUser;
  userRoles: string;

  /**
   *
   * @param dialogRef
   * @param data
   * @param {UsersService} userService
   */
  constructor(
    public dialogRef: MatDialogRef<UserDetailsModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private userService: UsersService
  ) {
    super();
  }

  ngOnInit() {
    this.userService.getUserById(this.data.userId).subscribe((res: any) => {
      this.user = res.data;
      const { roles } = this.user;
      this.loading = false;
      // const roleNames = [];
      // roles.forEach((role, key) => {
      //   if (role) {
      //     roleNames.push(role.title);
      //   }
      // });
      // this.userRoles = roleNames.join(",");
    });
  }

  /**
   * Handle no button clicked.
   */
  onNoClick(): void {
    this.dialogRef.close();
  }
}
