import { createReducer, on } from "@ngrx/store";
import { IBusinessType, IPagination } from "@shared/interfaces";
import { IBusiness } from "@shared/interfaces/business";
import * as BusinessActions from "../actions/business.actions";
import { IParameters } from "@shared/interfaces";

export const businessFeatureKey = "business";

export interface BusinessState {
  list: Array<any>;
  detail: IBusiness;
  businessTypes: IBusinessType[];
  pagination: IPagination;
  loading: boolean;
  message: string;
  parameters: IParameters;
  error: Error;
}

export const initialBusinessState: BusinessState = {
  list: [],
  businessTypes: [],
  pagination: null,
  loading: false,
  message: "",
  detail: null,
  parameters: null,
  error: null,
};

export const businessReducer = createReducer(
  initialBusinessState,

  on(BusinessActions.setParameters, (state, action) => ({
    ...state,
    parameters: action.params,
  })),
  on(BusinessActions.loadBusinesses, (state) => ({
    ...state,
    loading: true,
  })),
  on(BusinessActions.loadBusinessesSuccess, (state, action) => ({
    ...state,
    list: action.data,
    pagination: action.pagination,
    message: action.message,
    loading: false,
  })),
  on(BusinessActions.loadBusinessesFailure, (state, action) => ({
    ...state,
    loading: false,
  })),
  on(BusinessActions.getBusiness, (state) => ({ ...state, loading: true })),
  on(BusinessActions.getBusinessSuccess, (state, action) => ({
    ...state,
    detail: action.data,
    message: action.message,
    loading: false,
  })),
  on(BusinessActions.getBusinessesFailure, (state, action) => ({
    ...state,
    loading: false,
  })),
  on(BusinessActions.getBusinessTypes, (state) => ({
    ...state,
    loading: true,
  })),
  on(BusinessActions.getBusinessTypesSuccess, (state, action) => ({
    ...state,
    businessTypes: action.data,
    message: action.message,
    loading: false,
  })),
  on(BusinessActions.getBusinessesTypesFailure, (state, action) => ({
    ...state,
    loading: false,
  })),
  on(BusinessActions.saveBusiness, (state) => ({ ...state, loading: true })),
  on(BusinessActions.saveBusinessSuccess, (state, action) => ({
    ...state,
    message: action.message,
    loading: false,
  })),
  on(BusinessActions.saveBusinessesFailure, (state, action) => ({
    ...state,
    loading: false,
    error: action.error,
  })),
  on(BusinessActions.updateBusiness, (state) => ({ ...state, loading: true })),
  on(BusinessActions.updateBusinessSuccess, (state, action) => ({
    ...state,
    message: action.message,
    loading: false,
  })),
  on(BusinessActions.updateBusinessesFailure, (state, action) => ({
    ...state,
    loading: false,
  })),
  on(BusinessActions.deleteBusiness, (state) => ({ ...state, loading: true })),
  on(BusinessActions.deleteBusinessSuccess, (state, action) => ({
    ...state,
    message: action.message,
    loading: false,
  })),
  on(BusinessActions.deleteBusinessesFailure, (state, action) => ({
    ...state,
    loading: false,
  })),

  on(BusinessActions.createUserForBusiness, (state: BusinessState) => ({
    ...state,
    loading: true,
  })),

  on(BusinessActions.createUserForBusinessSuccess, (state: BusinessState, action) => ({
    ...state,
    loading: false,
    message: action.message,
  })),

  on(BusinessActions.createUserForBusinessFailure, (state: BusinessState, action) => ({
    ...state,
    loading: false,
    message: action.message,
    error: action.error,
  }))
);
