import { CommonModule } from "@angular/common";
import { ModuleWithProviders, NgModule, Optional, SkipSelf } from "@angular/core";
import { environment } from "@environments/environment";
import { StoreModule } from "@ngrx/store";
import { BaseComponent } from "./components";
import { httpInterceptorProviders } from "./interceptors";
import { ConstantsService } from "./services";
import { coreReducers } from "./store/reducers/index";

@NgModule({
  declarations: [BaseComponent],
  imports: [CommonModule, StoreModule.forFeature("coreModule", coreReducers)],
  exports: [],
  providers: [ConstantsService, httpInterceptorProviders, { provide: "BASE_API_URL", useValue: environment.apiUrl }],
})
export class CoreModule {
  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    if (parentModule) {
      throw new Error("CoreModule is already loaded. Import it in the AppModule only");
    }
  }

  static forRoot(): ModuleWithProviders<CoreModule> {
    return {
      ngModule: CoreModule,
      providers: [],
    };
  }
}
