import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from "@angular/core";
import { MatPaginator, PageEvent } from "@angular/material/paginator";
import { BaseComponent } from "@core/components";
import { StorageKeys } from "@core/enums";
import { Store } from "@ngrx/store";
import { IParameters } from "@shared/interfaces";
import { IUser } from "@shared/interfaces/user/user";
import { Observable } from "rxjs";
import { SearchInputComponent } from "./../../../search-input/search-input.component";

/**
 * @title Dialog Overview
 */
@Component({
  selector: "app-users-list",
  templateUrl: "users-list.component.html",
})
export class UsersListComponent extends BaseComponent implements OnInit {
  @ViewChild("searchInputCmp") searchInputCmp: SearchInputComponent;
  @Input() users$: Observable<IUser[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @Input() pagination$: Observable<IParameters>;
  @Output() addUserClicked: EventEmitter<null> = new EventEmitter<null>();
  @Output() paramsChanged: EventEmitter<IParameters> = new EventEmitter<IParameters>();
  @Output() viewUserDetailsClicked: EventEmitter<number> = new EventEmitter<number>();
  @Output() editUserDetailsClicked: EventEmitter<number> = new EventEmitter<number>();
  searchText$: Observable<string>;
  pagination: any;
  pageEvent: PageEvent;
  usersCount: number;

  /**
   *
   */
  constructor(private store: Store<any>) {
    super();
  }

  ngOnInit() {
    this.users$.subscribe((users: IUser[]) => {
      this.usersCount = users.length;
    });
    this.pagination$.subscribe((params: IParameters) => {
      if (params) {
        this.pagination = params;
      }
    });
    this.searchText$ = this.store.select((store) => store.sharedModule.users.filterParams.search);
  }

  /**
   * Emit an event to show the user add modal.
   */
  handleModalShow() {
    this.addUserClicked.emit();
  }

  /**
   *
   * @param e
   * @returns {any}
   */
  trackPaginator(e: any) {
    if (this.paginator) {
      this.storage.set(StorageKeys.PER_PAGE, e.pageSize);
      const params: IParameters = {
        page: e.pageIndex + 1,
        size: e.pageSize,
        search: "",
      };

      this.paramsChanged.emit(params);
    }

    return e;
  }

  /**
   *
   * @param {string} search
   */
  handleSearch(search: string) {
    this.paramsChanged.emit({
      page: this.paginator.pageIndex,
      size: this.paginator.pageSize,
      search,
    });
  }

  /**
   *
   * @param userId
   */
  showUserDetails(userId: number) {
    this.viewUserDetailsClicked.emit(userId);
  }

  /**
   *
   * @param {number} userId
   */
  onUserEdit(userId: number) {
    this.editUserDetailsClicked.emit(userId);
  }

  resetSearchInput() {
    this.searchInputCmp.searchInput.nativeElement.value = "";
  }
}
